const addHighlightEffect = (id) => {
  const target = document.getElementById(id);
  if (target) {
    target.classList.add("highlight");
    setTimeout(() => {
      target.classList.remove("highlight");
    }, 1000); // Thi gian khp vi animation 1s
  }
};

