import React, { useState } from "react";
import "./TeamMembers.css";
import { ImgApps } from "../../constant/images";

const members = [
  {
    id: 1,
    name: "Bi Th Mai Anh - Khoa SPTA",
    image: ImgApps.buimaianh,
  },
  {
    id: 2,
    name: "Cao Vn Anh - Khoa NN&VH Hn Quc",
    image: ImgApps.caovananh,
  },
  {
    id: 3,
    name: "Hong Khnh An - Khoa NN&VH Php",
    image: ImgApps.hoangankhanh,
  },
  {
    id: 4,
    name: "L Hoi An - SPTA",
    image: ImgApps.lehoaian,
  },
  {
    id: 5,
    name: "Nguyn Diu An - SPTA",
    image: ImgApps.nguyendieuan,
  },
  {
    id: 6,
    name: "Nguyn Thanh H An - NN&VH Nht Bn",
    image: ImgApps.thanhan,
  },
];

function TeamMembers() {
  const [selectedMember, setSelectedMember] = useState(null);

  const handleCardClick = (member) => {
    setSelectedMember(member);
  };

  const closePopup = () => {
    setSelectedMember(null);
  };

  return (
    <div className="TeamMembers">
      <h1 style={{ textAlign: "center", marginTop: "30px" }}>
        i ng thc hin
      </h1>
      <div className="team-container">
        {members.map((member) => (
          <div
            key={member.id}
            className="team-card"
            onClick={() => handleCardClick(member)}
          >
            <img src={member.image} alt={member.name} />
            <div className="team-name">{member.name}</div>
          </div>
        ))}
      </div>

      {selectedMember && (
        <div className="popup-overlay" onClick={closePopup}>
          <div className="popup-content" onClick={(e) => e.stopPropagation()}>
            <img src={selectedMember.image} alt={selectedMember.name} />
            <h2>{selectedMember.name}</h2>
          </div>
        </div>
      )}
    </div>
  );
}

export default TeamMembers;
